import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {HttpClientModule} from '@angular/common/http';
import { HttpModule } from '@angular/http';

import {
  ReactiveFormsModule,
  FormsModule,
  FormGroup,
  FormControl,
  Validators,
  FormBuilder
} from '@angular/forms';
import {AppRoutingModule} from './app-routing.modules';
// import { MatProgressBarModule } from "@angular/material";
import { AppComponent } from './app.component';
import { NewRequestComponent } from './new-request/new-request.component';
import { ExistingComponent } from './existing/existing.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { FeedbackComponent } from './feedback/feedback.component';
import { ProductComponent } from './product/product.component';
import {ProductService} from './product/product.service';
import {TransactionService} from './services/transaction.service';
import { CommonelementComponent } from './commonelement/commonelement.component';
import {myDirectiveInfo} from './directives/inputDir';


@NgModule({
  declarations: [
    AppComponent,
    NewRequestComponent,
    ExistingComponent,
    HeaderComponent,
    FooterComponent,
    FeedbackComponent,
    ProductComponent,
    CommonelementComponent,
    myDirectiveInfo,

  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule, 
    HttpModule
  ],
  providers: [ProductService,TransactionService],
  bootstrap: [AppComponent]
})
export class AppModule { }



