import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewRequestComponent } from './new-request/new-request.component';
import { ExistingComponent } from './existing/existing.component';
import { FeedbackComponent } from './feedback/feedback.component';
import { ProductComponent } from './product/product.component';
import {CommonelementComponent} from './commonelement/commonelement.component';

const routes: Routes = [
  { path: '', redirectTo: '/newRequest', pathMatch: 'full' },
  { path: 'newRequest', component: NewRequestComponent },
  { path: 'existingRequest', component: ExistingComponent },
  {path:'feedback', component:FeedbackComponent},
  {path:'product', component:ProductComponent},
   {path:'common',component: CommonelementComponent}
 
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}


