import { Component, OnInit } from '@angular/core';
import {
  ReactiveFormsModule,
  FormsModule,
  FormGroup,
  FormControl,
  Validators,
  FormBuilder
} from '@angular/forms';

@Component({
  selector: 'app-existing',
  templateUrl: './existing.component.html',
  styleUrls: ['./existing.component.css']
})
export class ExistingComponent implements OnInit {

 HEROES:any[];
 data:any[];
 langs: string[] = [
  'English',
  'French',
  'German',
];
myform: FormGroup;
firstName: FormControl;
lastName: FormControl;
email: FormControl;
password: FormControl;
language: FormControl;
  constructor() { }

  ngOnInit() {

    this.createFormControls();
    this.createForm();
   // this.onClickSubmit();

    this.data= [
      {
        "created_at": 1518847480422,
        "updated_at": 1518847480471,
        "remaining_quantity": 14,
        "price_per_unit": 71.67
      },
      {
        "created_at": 1518847470499,
        "updated_at": 1518847470499,
        "remaining_quantity": 218,
        "price_per_unit": 71.63
      },
      {
        "created_at": 1518847468295,
        "updated_at": 1518847468295,
        "remaining_quantity": 825,
        "price_per_unit": 71.62
      },
      {
        "created_at": 1518847174440,
        "updated_at": 1518847406250,
        "remaining_quantity": 2259,
        "price_per_unit": 71.6
      }
    ];

   

    }
    createFormControls() {
      this.firstName = new FormControl('', Validators.required);
      this.lastName = new FormControl('', Validators.required);
      this.email = new FormControl('', [
        Validators.required
        // Validators.pattern("[^ @]*@[^ @]*")
      ]);
      this.password = new FormControl('', [
        Validators.required,
        Validators.minLength(8)
      ]);
     // this.language = new FormControl('');
    }
  
    createForm() {
      this.myform = new FormGroup({
        name: new FormGroup({
          firstName: this.firstName,
          lastName: this.lastName,
        }),
        email: this.email,
        password: this.password
       // language: this.language
      });
    }

    onClickSubmit(data) {
      console.log(data);
    }

    actionOnSubmit(form){
      console.log(form);
    }

    }

   
  

  



